@import url(https://fonts.googleapis.com/css2?family=Dancing+Script:wght@700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Comic+Neue:wght@700&display=swap);
@import url(https://fonts.googleapis.com/css?family=Liu+Jian+Mao+Cao|Neucha|Patrick+Hand&display=swap);
body {
  background-image: url(/cursor-react7/static/media/fog.8e1199e9.jpg);
  background-repeat: around;
  background-size: 100%;
}
.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

* {
  padding: 0;
  margin: 0;
}
body {
  height: 1000px;
}
.menu {
  width: 100%;
  margin-top: 15px;
}
.menu ul {
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  justify-content: space-around;
}
li {
  list-style: none;
  width: 13%;
  height: 35px;
  border-top: 0px;
  text-align: center;
}
.link {
  text-decoration: none;
  color: #006600;
  font-size: 30px;
  font-family: "Dancing Script", cursive;
}
.link:hover {
  color: #0d3300;
  text-shadow: 0.1px 0.1px 0.1px #0d3300, 0 0 1em rgb(12, 122, 27);
}

.post {
  width: 45%;
  height: 450px;
  display: flex;
  background-color: whitesmoke;
  border: 1px solid #003300;
  border-radius: 12px;
  flex-direction: row;
  flex-wrap: wrap;
  margin-bottom: 100px;
}
.imgPost {
  margin-left: 50px;
}
.imgPost img {
  width: 85%;
  border-radius: 8%;
  height: 270px;
  margin-top: 10px;
}
.mainInfAutor,
.imgPost {
  width: 90%;
  margin: auto;
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
}
.icons {
  width: 90%;
  margin: 10px 70px;
  display: flex;
  flex-direction: row;
}
.icons img {
  width: 35px;
  height: auto;
  -webkit-filter: contrast(150%);
          filter: contrast(150%);
}
#photo {
  padding-top: 10px;
  width: 30%;
}
#photo img {
  width: 100px;
  height: 100px;
  border-radius: 100%;
}
#name,
#msg {
  font-size: 33px;
  font-family: "Patrick Hand", cursive;
}
#nickname,
#data {
  margin-top: 20px;
  font-size: 25px;
  font-family: "Liu Jian Mao Cao";
}
#data {
  font-family: "Neucha", cursive;
}
#msg {
  font-size: 20px;
  padding-top: 10px;
}
.icons span {
  font-size: 25px;
  font-family: "Patrick Hand", cursive;
  margin-right: 70px;
}

.postBlock {
  width: 100%;
  height: auto;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: space-around;
  margin-top: 50px;
}

.addUser {
  width: 40%;
  height: auto;
  flex-direction: row;
  flex-wrap: wrap;
  margin: 40px 30%;
}
.addUser input,
button {
  width: 40%;
  margin: 10px 20px;
  height: 30px;
  border-radius: 12px;
  color: rgb(5, 43, 5);
  outline: none;
  padding-left: 10px;
}
#avatar {
  width: 90%;
}
.addUser button {
  width: 30%;
  padding-left: 0;
  color: rgb(5, 43, 5);
  font-family: Verdana, Geneva, Tahoma, sans-serif;
  font-size: 20px;
}

